/* General Layout */
.dashboard-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f4f6f8;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  transition: margin-left 0.3s ease;
}
.top-bar {
  display: flex;
  align-items: center;
}
/* Header Layout */
.header {
  background: linear-gradient(to right, #4A90E2, #5BC0EB);
  padding: 20px;
  display: flex;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

/* Sidebar */
.sidebar {
  background-color: #2C3E50;
  width: 200px;
  height: 100vh;
  padding: 20px;
  position: fixed;
  top: 64px;
  left: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease; 
  z-index: 10;
}

.sidebar.visible {
  transform: translateX(0);
}

.sidebar-item {
  color: #FFFFFF;
  padding: 15px;
  cursor: pointer;
  transition: background 0.3s;
}

.sidebar-item:hover {
  background-color: #34495E;
}

.sidebar-item.active {
  background-color: #4A90E2;
}

/* Content Area Adjustments */
.dashboard-container.with-sidebar {
  margin-left: 200px;
  width: calc(100% - 200px);
}

.dashboard-container.full-width {
  margin-left: 0;
  width: 100%;
}

/* Sidebar Toggle Button */
.toggle-button {
  font-size: 1.5em;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  margin: 0 80px 0 100px;
}

/* Search and User Info in Header */
.search-container {
  flex-grow: 1;
  margin: 0 20px;
}

.search-bar {
  padding: 8px;
  border: none;
  border-radius: 4px;
  width: 100%;
}

.user-info {
  display: flex;
  align-items: center;
}

.notification-icon {
  font-size: 1.5em;
  margin-right: 10px;
}

.user-name {
  color: #FFFFFF;
  margin-right: 10px;
}

.logout-button {
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  background-color: #FF4D4D;
  color: white;
  cursor: pointer;
}

.logout-button:hover {
  background-color: #FF1A1A;
}

/* Stats and Cards */
.stats-card {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  margin-top: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
}

.stats-item {
  text-align: center;
  color: #2d3748;
}

.stats-item h5 {
  margin: 0;
  font-size: 1.5em;
  color: #3182ce;
}

.stats-item p {
  margin: 5px 0 0;
  font-size: 1em;
  color: #4a5568;
}

/* Tables */
.table-container {
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  overflow-x: auto;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 1em;
  text-align: left;
}

.data-table th,
.data-table td {
  padding: 12px 15px;
  border: 1px solid #ddd;
}

.data-table th {
  background-color: #3182ce;
  color: #ffffff;
  font-weight: bold;
}

.data-table tr:nth-child(even) {
  background-color: #f3f4f6;
}
.chart-container {
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
}
/* Responsive Containers for Charts and Tables */
.chart-container,
.data-table-container {
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
}

/* Layout for Main Content and App Container */
.app-container {
  display: flex;
}

.main-content {
  display: flex;
  flex-grow: 1;
}

/* Media Query for Mobile Screens (600px or smaller) */
@media screen and (max-width: 600px) {
  
  .top-bar{
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }

  .toggle-button {
    margin: 0;
  }

  .sidebar {
    width: 100vw; 
    position: absolute;
    z-index: 1000; 
  }
  aside.sidebar.visible,aside.sidebar.hidden {
    top: 55px;
}
  .dashboard-container.with-sidebar {
    margin-left: 0;
    width: 100%;
  }

  .header-content {
    flex-direction: column;
  }

  .search-container {
    width: 100%;
    margin: 10px 0;
  }

  .stats-card,
  .chart-container,
  .data-table-container {
    width: 100%; 
    padding: 10px;
    box-shadow: none; 
  }

 
  .data-table th, 
  .data-table td {
    font-size: 0.9em; 
    padding: 8px;
  }

  body {
    padding: 0;
    margin: 0;
  }
}
