body {
  font-family: 'Roboto', sans-serif;
  background-color: #e2e8f0;
  margin: 0;
  overflow: hidden;
  height: 100vh;
}

h1 {
  color: #2d3748;
}

h5, p {
  margin: 0;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #e2e8f0;
}
div#root {
  width: 100%;
}
.app-container {
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #3182ce;
  color: white;
  padding: 1rem;
  text-align: center;
  margin-bottom: 0;
}

.main-content {
  display: flex;
  flex: 1;
  background-color: #f4f6f8;
}

.sidebar {
  width: 200px;
  background-color: #2d3748;
  color: white;
  padding: 1rem;
}

.sidebar nav ul {
  list-style: none;
}

.sidebar nav ul li {
  margin: 15px 0;
  cursor: pointer;
}

.dashboard-container {
  flex: 1;
  padding: 20px 0px 64px;
  overflow-y: auto;
  height: 100vh;
}

.footer {
  background-color: #3182ce;
  color: white;
  text-align: center;
  padding: 1rem;
  width: 100%;
}
